.popup {
  display: none;
  position: fixed;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  max-width: 639px;
  max-height: 460px;
  height: 100%;
  width: 100%;
  background: #fafafa;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
  border-radius: 4px;
  z-index: 100;
}

.popup__body {
  padding: 33px 19px 0px 21px;
}

.popup__settings {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 98px;
}

.popup__stroke {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 328px;
  height: 56px;
  background: rgba(33, 33, 33, 0.08);
  border-radius: 4px 4px 0px 0px;
  margin-bottom: 16px;
  padding: 8px 8px 8px 52px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.6);
}

.popup__stroke:last-child {
  margin-bottom: 0;
}

.popup__stroke-circle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(1, 135, 134, 0.08);
  position: relative;
}

.popup__stroke-rect {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 18px;
  height: 18px;
  background: rgba(33, 33, 33, 0.08);
}

.popup__stroke-rect_active {
  background: url(../../assets/images/rect-active.png);
}
.popup__label {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.4px;
  color: rgba(0, 0, 0, 0.38);
}
.popup__name,
.popup__surname,
.popup__email {
  height: 24px;
  background: transparent;
  border: none;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.15px;
  color: rgba(0, 0, 0, 0.87);
}

.popup__name {
}
.popup__surname {
}
.popup__email {
}

.popup__avatar {
  height: 198px;
  width: 198px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 54px - 19px;
}
.popup__avatar-pic {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.popup__buttons {
  text-align: right;
}
.popup__btn-add,
.popup__btn-cancel {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 1.25px;
  text-transform: uppercase;
  padding: 10px 16px;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.12), 0px 2px 2px rgba(0, 0, 0, 0.24);
  border: none;
  border-radius: 4px;
}
.popup__btn-add {
  background-color: #2196f3;
  color: #ffffff;
  margin-right: 17px;
}
.popup__btn-cancel {
  background-color: #e3f2fd;
  color: #2196f3;
}
