.container {
  max-width: 1000px;
  margin: 0 auto;
  margin-bottom: 125px;
  background: rgba(0, 0, 0, 0.05);
  border-radius: 5px;
}

.game {
  width: 100%;
  height: 100%;
  padding: 28px 144px 121px 144px;
}

.game__cards {
  width: 1000px - 144px * 2;
  height: 802px - 152px - 118px;
  display: grid;
  row-gap: 14px;
  column-gap: 24px;
}
.game__cards_beginner-level {
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
}
.game__cards_medium-level {
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(6, 1fr);
}
.game__win-popup-cover {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10;
}
.game__cards-container {
  position: relative;
}

.game__cards-cover {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
