.game__card-container {
  width: 100%;
  height: 100%;
  cursor: pointer;
  perspective: 30rem;
}
.game__card,
.game__front,
.game__back,
.game__cover {
  width: 100%;
  height: 100%;
  border-radius: 4px;
}

.game__card {
  position: relative;
  transform-style: preserve-3d;
  transition: all 0.4s;
  transform-origin: right;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
  border-radius: 4px;
  &.flipped {
    transform: rotateY(180deg) translateX(-100%);
    transform-origin: left;
  }
}

.game__front,
.game__back,
.game__cover {
  position: absolute;
  backface-visibility: hidden;
  top: 0;
  left: 0;
}
.game__front {
  background: url(../../../assets/images/back-of-card.jpg) center;
  &_correct {
    background: rgba(10, 207, 131, 0.5);
  }
  &_wrong {
    background: rgba(242, 78, 30, 0.5);
  }
}
.game__back {
  transform: rotateY(180deg);
  &_correct {
    background: rgba(10, 207, 131, 0.5);
  }
  &_wrong {
    background: rgba(242, 78, 30, 0.5);
  }
}

.game__back-pic {
  position: absolute;
  width: 100%;
  height: 100%;
}

.game__cover {
  transform: rotateY(180deg);
  &_correct {
    background: rgba(10, 207, 131, 0.5);
  }
  &_wrong {
    background: rgba(242, 78, 30, 0.5);
  }
}
