.game__timer {
  position: relative;
  margin: 0 auto;
  width: 256px;
  height: 64px;
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.24);
  margin-bottom: 57px;
}

.game__time {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  letter-spacing: 0.15px;
  color: rgba(0, 0, 0, 0.4);
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 36px;
  line-height: 24px;
}
