.score__player {
  display: flex;
  align-items: center;
  padding-bottom: 5px;
  border-bottom: 1px solid rgba(33, 33, 33, 0.08);
  margin-bottom: 18px;
}
.score__player-pic {
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 16px;
}

.score__player-name {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  letter-spacing: 0.1px;
  color: #333333;
}
.score__player-email {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 0.4px;
  color: #333333;
}
.score__player-result {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.15px;
  color: rgba(0, 0, 0, 0.87);
  margin: 0 14px 0 auto;
}
.score__player-count {
  color: #2196f3;
}
