.container {
  max-width: 1000px;
  height: 802px;
  margin: 0 auto;
  margin-bottom: 125px;
  background: rgba(0, 0, 0, 0.05);
  border-radius: 5px;
}

.rules {
  padding: 28px 184px 85px 36px;
}
.rules__title {
  font-family: Roboto;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.15px;
  color: rgba(0, 0, 0, 0.87);
  mix-blend-mode: normal;
  margin-bottom: 20px;
}

.rules__body {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.rules__windows {
}
.rules__window {
  width: 360px;
  background: #ffffff;
  padding: 16px;
  display: flex;
}

.rules__window:first-child {
  height: 230px;
  margin-bottom: 16px;
}
.rules__window:nth-child(2) {
  height: 132px;
  margin-bottom: 21px;
}
.rules__window:nth-child(3) {
  height: 240px;
}
.rules__window-circle {
  position: relative;
  min-width: 40px;
  height: 41px;
  background: #2196f3;
  border-radius: 50%;
  margin-right: 16px;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12), 0px 2px 4px rgba(0, 0, 0, 0.14);
}

.rules__window-number {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 24px;
  letter-spacing: 0.25px;
  color: #ffffff;
}

.rules__window-text {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.25px;
  color: rgba(0, 0, 0, 0.6);
}

.rules__imgs-examples {
  text-align: right;
}
.rules__img-example:first-child {
  margin-bottom: 28px;
}
.rules__img-example:nth-child(2) {
  margin-bottom: 80px;
}

.rules__cover {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10;
}

.popup__title {
  padding: 20px 16px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  letter-spacing: 0.15px;
  color: rgba(0, 0, 0, 0.87);
  background: #ffffff;
}
