@mixin textSetting($fontSize, $lineHeight, $isBold) {
  font-family: Roboto;
  font-style: normal;
  @if $isBold == bold {
    font-weight: 500;
  } @else {
    font-weight: normal;
  }
  font-size: $fontSize;
  line-height: $lineHeight;
}

$main-color: #2196f3;
$second-color: #ffffff;

.header {
  height: 64px;
  background: $main-color;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.24);
  margin-bottom: 33px;
}

.header__container {
  height: 100%;
  margin-left: 24px;
  margin-right: 20px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.header__logo {
  margin-right: 152px;
}
.header__logo-part {
  width: 80px;
  height: 20px;
  @include textSetting(11px, 16px, normal);
  letter-spacing: 1.25px;
  text-transform: uppercase;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__logo-part:first-child {
  background: transparent;
  color: $second-color;
}
.header__logo-part:last-child {
  background: $second-color;
  color: $main-color;
}

.header__btn-and-user-icon {
  margin: 0 0 0 auto;
}
.header__btn {
  background: #f2f9fe;
  box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.2), 0px 1px 18px rgba(0, 0, 0, 0.12), 0px 6px 10px rgba(0, 0, 0, 0.14);
  border: none;
  border-radius: 4px;
  padding: 10px 16px;
  @include textSetting(14px, 16px, bold);
  letter-spacing: 1.25px;
  text-transform: uppercase;
  color: $main-color;
}

.header__img-avatar {
  width: 40px;
  height: 40px;
  margin-left: 30px;
  border-radius: 50%;
}
