.win-popup {
  display: none;
  position: fixed;
  top: 50vh;
  left: 50vw;
  transform: translate(-50%, -50%);
  max-width: 360px;
  max-height: 112px;
  height: 100%;
  width: 100%;
  background: hsl(0, 0%, 100%);
  z-index: 100;
  &__text {
    margin: 20px 16px 8px 16px;
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.25px;
    color: rgba(0, 0, 0, 0.6);
  }
  &__btn {
    display: block;
    margin: 0 auto;
    margin-bottom: 8px;
    padding: 10px 16px;
    border: none;
    background: #2196f3;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.12), 0px 2px 2px rgba(0, 0, 0, 0.24);
    border-radius: 4px;
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 1.25px;
    text-transform: uppercase;
    color: #ffffff;
  }
}
