.container {
  max-width: 1000px;
  margin: 0 auto;
  margin-bottom: 125px;
  background: #ffffff;
  box-shadow: 0px 6px 5px rgba(0, 0, 0, 0.24), 0px 9px 18px rgba(0, 0, 0, 0.18);
}

.settings {
  padding: 32px 0px 47px 46px;
}
.settings__item {
  width: 256px;
  padding-bottom: 16px;
  border-bottom: 1px solid #e5e5e5;
  margin-bottom: 37px;
  cursor: pointer;
}

.settings__item:last-child {
  margin-bottom: 0;
}
