.header__nav {
  height: 100%;
}
.header__items {
  height: 100%;
  display: flex;
}
.header__item {
  height: 100%;
  width: 96px;
  opacity: 0.7;
}

.header__item_selected {
  background: #1e88e5;
  opacity: 1;
}

.header__item-link {
  display: block;
  width: 100%;
  height: 100%;
  text-decoration: none;
  padding-top: 11px;
}
.header__item-body {
  width: 100%;
  height: calc(100% - 11px);
}
.header__circle {
  position: relative;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin: 0 auto;
  margin-bottom: 9px;
}
.header__pic {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.header__item-phrase {
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  color: #ffffff;
  letter-spacing: 0.4px;
  text-align: center;
}
